class UIcon extends Component {
  state = {}
  
  render() {
    const {
      className,
      size,
      style,
      icon,
      color,
      loading,
      onClick,
      title,
      svgRef,
    } = this.props;
    
    return (
      <svg
        ref={svgRef}
        className={className}
        viewBox="0 0 24 24"
        onClick={onClick}
        title={title || undefined}
        style={{
          verticalAlign: 'middle',
          height: `${size}px`,
          width: `${size}px`,
          ...style,
        }}
      >
        {loading ? (
          <path d={ICONS.LOADING} style={{ fill: color, verticalAlign: 'middle' }} />
        ) : (
          <path d={icon} style={{ fill: color, verticalAlign: 'middle' }} />
        )}
      </svg>
    );
  }
}

UIcon.defaultProps = {
  size: 16,
  color: 'currentColor',
};

const Icon = styled(UIcon)`
  ${props => props.loading && 'animation: spin 800ms linear infinite;'}
`;

export default Icon;
